.page {

  scroll-behavior: smooth;

  &__body {
    font-family: "Poppins", sans-serif;
    margin: 0;
    min-width: 320px;
    background-color: $background-color;
  }

  &:has(.page__menu:target) {
    overflow: hidden; /* not to scroll the page */
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  .page:has(.page__product-card:target) {
    overflow: hidden; /* not to scroll the page */
  }

  &__product-card {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
